import { Component, OnInit, AfterViewInit, ViewEncapsulation, Input, OnChanges, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { CalculatorAnalytics } from '../../services/calculator-analytics.service';
import { Subscription } from 'rxjs/Subscription';
import { FeatureAuthService, CookieService } from '../../../../../shared/services/index';
import { environment } from '../../../../../../environments/environment';
declare var jQuery: any;
declare var moment: any;
declare var Math: any;
declare var window: any;
@Component({
  selector: 'og-user-details',
  templateUrl: './user_details.component.html',
  encapsulation: ViewEncapsulation.None,
})

export class UserDetailsComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy {
  @Input() calc: any;
  // dataSet: any[] = [];
  avgAnswered: any = 0;
  avgResult: any = 0;
  avgLengthTime: string;
  socialEgagement: string;
  totalTime: number = 0;
  key: any;
  mostViewedVia: string;
  mostViewedOn: string;
  dataTableRef: any;
  visitorKey: string;
  limit_alert: boolean = false;
  leadLimit: Number;
  calc_id: string;
  disable: boolean = false;
  loader: Number = 1;
  postData: any = {
    start_date: moment().subtract(10, 'days').format('YYYY-MM-DD'),
    end_date: moment().add(1, 'day').format('YYYY-MM-DD')
  };
  viewedOnStats: any = {
    desktop: 0,
    mobile: 0
  };

  viewedViaStats: any = {
    facebook: 0,
    twitter: 0,
    direct: 0
  };

  private subs: Subscription[] = [];
  private lead: boolean = true;

  constructor(
    private _calculatorAnalytics: CalculatorAnalytics,
    private _featureAuthService: FeatureAuthService,
    private _cookieService: CookieService,
    private cdr: ChangeDetectorRef) {
  }

  ngAfterViewInit() {
    let self = this;
    jQuery(document).on('click', '.vd', (event: any) => {
      self.visitorKey = jQuery(event.target).data('key');
      // Slide right
      event.stopPropagation();
      setTimeout(() => {
        this.cdr.detectChanges();
        jQuery('.user-detail-outer').fadeIn();
      }, 100);
    });
    this.intializeDatatable();
  }

  checkLeadsLimit() {
    let storage = JSON.parse(this._cookieService.readCookie('storage'));
    let company = storage.company;
    this.limit_alert = company.current_limit.leads === -1 || company.current_limit.leads > company.current_usage.leads ? false : true;
    //this.limit_alert  = true;
  }

  ngOnInit() {
    // if (this.calc.spredsheetUrl) {
    //   this.exportToGoogleSheet();
    // }
    this.checkLeadsLimit();
  }

  ngOnChanges() {
    if (this.calc) {
      if (this.calc.liveApp)
        this.calc_id = this.calc._id;
      else
        this.calc_id = this.calc.parentApp;
      this.intializeDatatable();
      this.loader = 0;
      this.reInitAvgVariable();
    }
  }

  calulateAnalyticsData(data: any[]) {
    console.log('data', data);
    if (data.length) {
      data.map((response) => {
        if (response[7] != '--' || response[9] != '--') {
          this.viewedOnStats[response[7]] += 1;
          this.viewedViaStats[response[9] == '(not set)' ? 'direct' : response[9]] += 1;
          this.mostViewedVia = Object.keys(this.viewedViaStats).reduce((a: any, b: any) => this.viewedViaStats[a] > this.viewedViaStats[b] ? a : b);
          this.mostViewedOn = Object.keys(this.viewedOnStats).reduce((a: any, b: any) => this.viewedOnStats[a] > this.viewedOnStats[b] ? a : b);
        }
      });
    } else {
      this.mostViewedVia = '--';
      this.mostViewedOn = '--';
      this.avgLengthTime = '--';
      this.socialEgagement = '--';
    }
  }

  reInitAvgVariable() {
    this.mostViewedVia = '--';
    this.mostViewedOn = '--';
    this.avgLengthTime = '--';
    this.socialEgagement = '--';
    this.avgAnswered = '--';
    this.avgResult = '--';
  }

  intializeDatatable() {
    let self = this;
    this.dataTableRef = jQuery('#myTable').DataTable({
      "processing": true,
      "destroy": true,
      "serverSide": true,
      "paging": true,
      "pageLength": 10,
      "ajax": {
        "url": environment.API + "/analytic/get_leads",
        "type": "POST",
        "data": { id: self.calc_id, start_date: this.postData.start_date, end_date: this.postData.end_date, lead: this.lead },
        "dataSrc": function (response) {
          self.subs.push(self.avgAnalyticsData());
          self.loader = 1;
          return response.data;
        }
      },
      'sPaginationType': 'full_numbers',
      language: {
        emptyTable: '<center>No data available</center>',
        paginate: {
          "previous": '<i class="material-icons">skip_previous</i>',
          "first": '<i class="material-icons">keyboard_arrow_left</i>',
          "next": '<i class="material-icons">skip_next</i>',
          "last": '<i class="material-icons">keyboard_arrow_right</i>'
        }
      },
      columns: [
        { title: 'Name' },
        { title: 'Email' },
        { title: 'Location' },
        { title: 'Engagement' },
        { title: 'Questions Answered' },
        { title: (self.calc.templateType == 'Numerical') ? 'Result Obtained' : 'Outcome Obtained' },
        { title: 'Visited From' },
        { title: 'Visited On' },
        { title: 'Length of Visit' },
        { title: 'Source' },
        { title: '' }
      ]
    });
    this.dataTableRef.column(6).visible(false);

    if (this.lead.toString() == 'false') {
      this.dataTableRef.column(0).visible(false);
      this.dataTableRef.column(1).visible(false);
    }
    else {
      this.dataTableRef.column(2).visible(false);
    }
  }
  exportToGoogleSheet() {
    this.disable = true;
    jQuery('.export').html('Preparing your sheet..');
    jQuery('.update').html('Updating..');
    this._calculatorAnalytics.exportToSheet({ id: this.calc_id, spredsheetUrl: this.calc.spredsheetUrl }).subscribe(
      (response: any) => {
        // if (this.calc.spredsheetUrl === '') {
        window.open(this.calc.spredsheetUrl, '_blank');
        // }
        this.calc.spredsheetUrl = response;
        this.disable = false;
        jQuery('.export').html('<i class="material-icons">exit_to_app</i> &nbsp;View Sheet');
        jQuery('.update').html('<i class="material-icons">visibility</i>');
        //  window.location.href = response;
      },
      (error) => {
        console.log('error', error);
      }

    );
  }
  avgAnalyticsData() {
    let self = this;
    return this._calculatorAnalytics.getAvgOfLeads({ id: this.calc_id, start_date: this.postData.start_date, end_date: this.postData.end_date, lead: this.lead })
      .subscribe(
      (response: any) => {
        self.avgAnswered = Math.round(response.avgAnswers);
        self.avgResult = response.avgResult;
        let min = Math.floor(Number(response.avgTimeOnPage) / 60);
        let seconds = Math.round(Number(response.avgTimeOnPage) % 60);
        self.avgLengthTime = min ? (min + '.' + seconds + ' m') : seconds + ' s';
        self.socialEgagement = response.CtaEngagementLength;
        self.calulateAnalyticsData(response.data);
      },
      (error: any) => {
        console.log('Something Went Wrong in Avg leads!');
      });
  }

  onDateSelect(date: any) {
    this.postData.start_date = new Date(date.start_date).toISOString().substr(0, 10);
    this.postData.end_date = new Date(moment(new Date(date.end_date)).add(1, 'day').format('YYYY-MM-DD')).toISOString().substr(0, 10);
    //refresh stats
    if (this.calc_id) {
      this.intializeDatatable(); //this.subs.push(this.getLeadData(this.calc_id));
    }
  }

  leadDropdown(event: any) {
    this.lead = event;
    this.intializeDatatable();
  }

  ngOnDestroy() {
    this.unsubscribe();
  }

  unsubscribe() {
    this.subs.forEach(sub => sub.unsubscribe());
  }
}



